import LC from 'leancloud-storage';  // 导入 leancloud 数据存储模块
import BaseService from './base_service';  // 导入访问 leancloud 数据存储的基类


const TABLE_NAME = 'Topic';  // leancloud 中存储数据的表名(Class)

/**
 * 对leancloud应用中 名为 "Topic" 的 Class 进行数据操作的类
 */
class TopicService extends BaseService {
    constructor(table_name) {
        // 调用父类的构造函数
        super(table_name);
        // 当前子类的属性 this.TABLE_NAME
        this.TABLE_NAME = table_name;
    }
}


/**
 * 
 * @param {*} id 版块的id
 * @param {*} limit 返回的记录数
 * @returns 
 */
TopicService.prototype.getTopicByCategory = async function (id, limit = 5) {
    // console.log(`category id:`, id);
    try {
        let query = new LC.Query(this.TABLE_NAME);
        const board = LC.Object.createWithoutData('Category', id);
        let response = await query.equalTo('category', board)
            .descending('level')           // 降序
            .addDescending('createdAt')    // 第二个排序字段：创建时间降序
            .limit(limit)
            .find();
        console.log('getTopicByCategory:', response);
        return response
    } catch (e) {
        console.log('查询数据错误：', e);
    }
    return null;
}



// 导出子类时使用 new 直接进行实例化
export default new TopicService(TABLE_NAME);